.slide-container {
  margin-top: 80px;
}
.body{
  background-color: bisque;
}
.each-slide > div {
  display: flex;
  align-items: center;
  justify-content: center;
  background-size: cover;
}

.each-slide span {
  padding: 20px;
  font-size: 20px;
  background: #efefef;
  text-align: center;
}
.blogImage{
  width:90%;
  height:600px;
  margin-top: 20px;
    display: block;
      margin-left: auto;
      margin-right: auto;
      border-radius: 7px;
}
.uppereditor{
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin: 5em;
}

.editor {
  margin-right: 10em;
}

.ck-editor__editable_inline {
  min-height: 500px;
}
input[type=file]::file-selector-button {
  border: 2px solid ;  
  border-radius: 5px;
  background-color: #ff9100;
  /* background-color: rgb(41, 41, 41); */
  color: white;
  /* margin-bottom: 15px; */
}

.nav-menu-icon{
  display: none;
}
.nav-search-bar{
  display:flex;
  align-items: center;
  flex-grow: 1;
}
.nav-buttons{
  display: flex;
  align-items: center;
}
.blogcard-box{
  display:flex; 
  align-items: center;
  margin: 20px;
}
.blogcard-left-part{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 1;
}
.blogcard-image{
  background-color: "#1fef";
  height: 26vh;
  width: 20vw;
  z-index: 1;
  /* background-image:url(https://images.unsplash.com/photo-1536987333706-fc9adfb10d91?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1500&q=80); */
  background-repeat: no-repeat;
  background-size: 20vw 26vh;
  box-shadow: 3px 3px 8px grey;
  border-radius: 30px;
  display: flex;
  align-items: flex-end;
  /* justify-content: end; */
}
.blogcard-image:hover{
  transform:rotateY(10deg);
  /* transform: rotate(1turn); */
  /* transform:rotateY(15deg) rotateX(-15deg);
  transition-duration: 200ms; */
  box-shadow: 0px 0px 16px rgb(190, 182, 167);
}
.blogcard-info{
  width: 36vw;
  height: 32vh;
  align-items: center;
  margin-left: -11vw;
  /* box-shadow: 6px 6px 19px grey; */
  border: 1px solid grey;
}
.blogcard-info:hover{
  box-shadow: 0px 0px 16px rgb(190, 182, 167);
}
.blogcard-cardcontent{
  margin-left: 11vw;
  display: flex;
  flex-direction: column;
}
.blogcard-cardactions{
  margin-left:12vw;
  margin-top:2vh;
}
.blogcards{
  display:grid;
  grid-template-columns: 1fr 1fr;
  margin-left: 1vw;
}
.sidebar{
  width:25vw;
  margin-top: 140px; 
  margin-left: 71vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-image: linear-gradient(to left, red, #ff9100);
  border-radius: 5px;
  z-index: 200;
  padding-bottom: 10px;
  box-shadow: 2px 2px 8px rgb(215, 200, 155);
  position: fixed;
}
.sidebar-large{
  width:25vw;
  margin-top: 330px; 
  margin-left: 71vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-image: linear-gradient(to left, red, #ff9100);
  border-radius: 5px;
  z-index: 200;
  padding-bottom: 10px;
  box-shadow: 2px 2px 8px rgb(215, 200, 155);
  position: fixed;
}
.sidebar-buttons{
  width:85%;
  display: flex;
  justify-content: center;
  margin: 5px 0px;
  cursor: pointer;
  border-radius: 5px;
  background-color: #efefef;
}
.sidebar-buttons:hover{
  width:95%;
}
.sidebar-buttons>h3{
  color: black;
  margin: 5px;
}
.sidebar-close-icon{
  margin-top: 4px;
  margin-left: 88%;
}
.signup{
  width: 65vw;
  background-color:white;
  padding: 2%;
  display:flex;
  flex-direction:column;
  justify-content: center;
  border-radius: 15px;
  align-items: center;
  box-shadow: 0px 0px 8px rgb(181, 180, 180);
  /* background-image: url("./Images/istockphoto-475706356-170667a.jpg");
  background-repeat: no-repeat;
  background-size:cover; */
}
.profile-box{
  width: 50vw;
  background-color:white;
  padding: 5% 2%;
  display:flex;
  flex-direction:column;
  justify-content: center;
  border-radius: 15px;
  align-items: center;
  box-shadow: 2px 2px 6px grey;
  border:8px solid #ff9100;
}
.profile-image{
  width:15vw;
  height:15vw;
  border-radius: 50%;
  border:12px solid #efefef;
  background-size: 15vw 15vw;
  background-repeat: no-repeat;
}
.profile-mail-div{
  display: flex;
  align-items: center;
  justify-content: center;
  background-color:rgb(208, 227, 227);
  padding: 10px 14px;
  border-radius: 10px;
}
.profile-mail-div>h4{
  margin: 0px;
  padding: 0px;
}
.home-image-box{
  background:url("./Images/blog.jpg");
  background-repeat: no-repeat;
  height: 65vh;
  width: 100vw;
  background-size:100vw 70vh;
  margin-top: 70px;
  /* opacity: 85%; */
  display: flex;
  flex-direction: column;
  align-items: center;
  /* filter: blur(1px);
  -webkit-filter: blur(1px); */
  /* padding-top:8vh; */
}
.home-title{
  margin:0px;
  padding: 0px;
  /* margin-top: 42vh; */
  /* margin-right: 3vw; */
  font-style:italic;
  font-weight:bolder;
  color:white;
}
.about-page-image{
  background:url("./Images/about-us-sign-as-crossword-cube-blocks-white-background-3d-rendering_476612-13679.webp");
  background-repeat: no-repeat;
  height: 70vh;
  width: 100vw;
  background-size:100vw 70vh;
  margin-top: 70px;
  opacity: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.about-page-div{
  width:70vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color:rgb(208, 227, 227);
  padding: 10px 14px;
  border-radius: 10px;
  margin-top:30px;
}
.about-page-div:hover{
  box-shadow: 4px 4px 8px rgb(172, 172, 172);
}
.filters-list{
  display:flex;
  margin: 20px;
  overflow:auto;
  white-space: nowrap;
}
.filter-button{
  box-shadow: 2px 2px 6px grey;
  margin: 0px 0.5vw;
  font-size: 18px;
  font-weight: bold;
  border-radius:15px;
  padding:0px 15px;
  cursor: pointer;
}
.edit-request-div{
  display: flex;
  align-items: center;
  justify-content: center;
  background-color:rgb(236, 238, 213);
  padding: 10px 14px;
  border-radius: 10px;
}
@media screen and (max-width: 1090px) {
  .nav-menu-icon{
    display: block;
  }
  .nav-buttons{
    display:none;
  }
}
@media screen and (max-width: 1020px) {
  .blogcards{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .blogcard-image{
    width:40vw;
    height: 26vh;
    background-size: 50vw 26vh;
  }
  .blogcard-info{
    width: 66vw;
    margin-left: -20vw;
  }
  .blogcard-cardcontent{
    margin-left: 20vw;
  }
  .blogcard-cardactions{
    margin-left:22vw;
  }
}
