  .blogcard-box{
    flex-direction:column;
  }
  .blogcard-image{
    width:64vw;
    height: 22vh;
    background-size: 64vw 22vh;
  }
  .blogcard-info{
    width: 80vw;
    margin-left: 0;
    margin-top: -11vh;
  }
